// const domain = 'http://47.95.7.10:8080'
const domain = ''

export default {
  register: `${domain}/user`,
  login: `${domain}/login`,
  userInfo (user_id) {
    return `${domain}/user/${user_id}`
  },
  userFollowing (user_id) {
    return `${domain}/user/${user_id}/following`
  },
  userUnFollowing (user_id) {
    return `${domain}/user/${user_id}/unfollowing`
  },
  star (user_id) {
    return `${domain}/star/user/${user_id}`
  },
  userInfo (use_id) {
    return `${domain}/user/${use_id}`
  },
  starNews (star_id) {
    return `${domain}/star/${star_id}/news`
  },
  userNews (user_id) {
    return `${domain}/user/${user_id}/news`
  },
  starStates (star_id) {
    return `${domain}/star/${star_id}/states`
  },
  starHead (star_id) {
    return `${domain}/star/${star_id}/head`
  }
}



