import React, { Component } from 'react'

class Register extends Component {
  constructor (props) {
    super(props)

    this.state = {
      username: '',
      password: '',
      img: '',
      suv: ''
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  handleChange (type, event) {
    this.setState({
      ...this.state,
      [type]: event.target.value
    })
  }
  handleSubmit (event) {
    event.preventDefault()
    console.log('ok')
  }

  render () {
    return (
      <div>
        <form onSubmit={this.handleSubmit}>
          <label>
            用户名：
            <input type="text"
              value={this.state.username} onChange={this.handleChange.bind(this, 'username')}/>
          </label>
          <label>
            密码：
            <input type="text"
              value={this.state.password} onChange={this.handleChange.bind(this, 'password')}/>
          </label>
          <label>
            img：
            <input type="text"
              value={this.state.img} onChange={this.handleChange.bind(this, 'img')}/>
          </label>
          <label>
            suv：
            <input type="text"
              value={this.state.suv} onChange={this.handleChange.bind(this, 'suv')}/>
          </label>
          <input type="submit" value="注册"/>
        </form>
      </div>
    )
  }
}

export default Register



