import React, { Component } from 'react'

class NewsItem extends Component {
  render () {
    return (
      <div>
        <div>继续聊一下周琦</div>
        <div>
          <div>1分钟前</div>
          <div>一片与</div>
          <div>1235</div>
        </div>
      </div>
    )
  }
}

class Community extends Component {
  render () {
    return (
      <div>
        community
        <NewsItem></NewsItem>
        <NewsItem></NewsItem>
        <NewsItem></NewsItem>
        <NewsItem></NewsItem>
        <NewsItem></NewsItem>
      </div>
    )
  }
}

export default Community



